.form__label {
  display: block;
  margin: 15px 0 5px 0;
  color: #252525;
}

.form__input {
  width: 100%;
  padding: 10px;
  border: 1px #ddd solid;

  &:focus-visible {
    outline: none;
    border-color: #f7c08a;
  }

  &--textarea {
    height: 200px;
  }
}

.form__button {
  color: white;
  background-color: #353535;
  cursor: pointer;
  border: 0;
  margin: 30px 0 10px 0;

  &:hover {
    color: #353535;
    background-color: #f7c08a;
  }
}
